<template>
  <div class='main'>
    <div class='form'>
      <el-form :inline="true"
               :model="form"
               class="demo-form-inline">
        <el-form-item class='search'>
          <el-input placeholder=""
                    class="input-with-select"
                    v-model='form.search'>
            <el-button slot="append"
                       @click='search()'></el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker type="daterange"
                          align="right"
                          unlink-panels
                          range-separator="-"
                          start-placeholder=""
                          end-placeholder=""
                          @change="search()"
                          style="width: 100%;"
                          value-format="yyyy-MM-dd HH:mm:ss"
                          v-model='form.date'>
          </el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <div class='table'>
      <el-table :data="tableData"
                style="width: 100%"
                :max-height="screenHeight">
        <el-table-column prop="apply_sn"
                         label=""
                         align="center"></el-table-column>
        <el-table-column prop="create_time"
                         label=""
                         align="center"></el-table-column>
        <el-table-column prop="audit_time"
                         label=""
                         align="center"></el-table-column>
        <el-table-column prop="address"
                         label=""
                         align="center">
          <template slot-scope='scope'>
            <span>{{scope.row.audit_status==1 ? '' : ''}}</span>
          </template>
        </el-table-column>
        <el-table-column label=""
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       size="small"
                       @click='details(scope.row)'></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="address"
                         label=""
                         align="center">
          <template slot-scope="scope">
            <el-button @click="ReceivingGoods(scope.row,scope.$index)"
                       type="primary"
                       size="small"
                       :loading="loading[scope.$index]"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="sf"
                         label=""
                         align="center">
          <template slot-scope='scope'>
            <span>{{scope.row.if_delay==0 ? '' : ''}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog class='details'
               title=""
               :visible.sync="detailsDialogVisible"
               width="50%"
               center>
      <el-table :data="tableData2"
                border
                style="width: 100%">
        <el-table-column prop="apply_sn"
                         label=""
                         align='center'></el-table-column>
        <el-table-column prop="create_time"
                         label=""
                         align='center'></el-table-column>
        <el-table-column prop="audit_time"
                         label=""
                         align='center'>
        </el-table-column>
      </el-table>
      <el-table :data="tableData3"
                border
                style="width: 100%">
        <el-table-column prop="materiel"
                         label=""
                         align='center'></el-table-column>
        <el-table-column prop="remarks"
                         label="()"
                         align='center'></el-table-column>
        <el-table-column prop="num"
                         label=""
                         align='center'>
        </el-table-column>
      </el-table>
      <span slot="footer"
            class="dialog-footer">
        <el-button type="primary"
                   @click="confirmgGoods"></el-button>
      </span>
    </el-dialog>
    <component is='paging'
               :pageCount='pageCount'
               :currentChange='currentChange'
               :currentPage='currentPage'
               :sizeChange='sizeChange'></component>
  </div>
</template>
<script>
import { unConfirm, confirm_goods, detail, materialSearch } from '@/server/serverData'
import paging from '@/views/common/paging'
export default {
  data () {
    return {
      currentPage: 1, //
      pageCount: 1, //
      form: {
        search: '',
        date: ''
      },
      tableData: [],
      tableData2: [],
      tableData3: [],
      screenHeight: window.innerHeight - 270,
      detailsDialogVisible: false, //
      materielID: '',
      loading: []
    }
  },
  components: {
    paging
  },
  computed: {
    ApplyTime () {  /** ,,, **/
      return this.form.date instanceof Array ? {
        startTime: this.form.date[0],
        endTime: this.form.date[1]
      } : {
          startTime: '',
          endTime: ''
        }
    },
  },
  methods: {
    /**  **/
    async init () {
      let res = await materialSearch({
        value: this.form.search,
        startTime: this.ApplyTime.startTime,
        endTime: this.ApplyTime.endTime,
        page: this.currentPage
      })
      if (res.data.code == 1) {
        this.pageCount = res.data.data.rows;
        if (this.currentPage > this.pageCount) {
          this.currentPage = this.pageCount;
        }
        this.tableData = res.data.data.list;
      } else {
        this.tableData = []
        this.pageCount = 1
      }
    },

    /**  **/
    ReceivingGoods (row, index) {
      /** page-1 **/
      if (this.pageCount > 1 && this.currentPage == this.pageCount && this.tableData.length <= 1) this.currentPage -= 1;

      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.request(row.id, index)
      }).catch(() => { })
    },

    /**  **/
    async details (row) {
      this.detailsDialogVisible = true
      this.materielID = row.id
      let res = await detail({ id: row.id })
      if (res.data.code == 1) {
        let data = res.data.data;
        this.tableData2 = [
          { apply_sn: data.apply_sn, create_time: data.create_time, audit_time: data.audit_time }
        ]
        this.tableData3 = JSON.parse(data.detail)
        console.log(this.tableData3)
      }
    },
    /** **/
    confirmgGoods () {
      this.request(this.materielID)
    },
    /**  **/
    async  request (id, index) {
      this.loading[index] = true
      let res = await confirm_goods({ id: id })
      this.loading[index] = false
      if (res.data.code == 1) {
        let that = this;
        this.init()
        this.$message({          message: res.data.msg, type: 'success', duration: 1500, onClose () {
            that.detailsDialogVisible = false
            that.$store.commit('CHANGE_NUMBER', 'material')
          }        })
      } else {
        this.$message.error(res.data.msg)
      }
    },
    /**  **/
    search () {
      /** 1, **/
      this.currentPage = 1;
      this.pageCount = 1;
      this.init()
    },
    /**  **/
    currentChange (e) {
      this.currentPage = e
      this.init()
    },
    sizeChange (e) {
      console.log(123)
    }
  },
  mounted () {
    this.init()
    var that = this
    window.onresize = () => {
      return (() => {
        that.screenHeight = window.innerHeight - 370
      })()
    }
  }
}
</script>
<style scoped lang='scss' rel="stylesheet/scss">
.form {
  padding: 20px 0 20px 30px;
  margin-bottom: 0;
  /deep/ .el-form-item__content {
    .el-input-group__append,
    .el-input-group__prepend {
      border: none;
      background-color: #3498e9;
      color: white;
      padding: 0 30px;
    }
  }
  .el-form-item {
    margin-bottom: 0 !important;
  }
  .search {
    margin-right: 40px;
  }
}
.table {
  /deep/ .el-table th,
  .el-table tr {
    background-color: #d7e1e6;
  }
  /deep/ .el-table__row {
    td {
      .cell {
        font-size: 12px;
      }
    }
  }
}
.paging {
  text-align: center;
  margin-top: 30px;
}

/**/
.details {
  /deep/ .el-dialog__header {
    padding-bottom: 20px;
    .el-dialog__title {
      color: #32373c;
      font-size: 20px;
      font-family: PingFangSC-Semibold;
    }
  }
  /deep/ .el-dialog__body {
    padding-top: 0;
    .el-table {
      margin-bottom: 30px;
      .el-table__header {
        thead {
          th {
            background-color: #d7e1e6;
            border: none;
          }
        }
      }
    }
    .el-table:last-child {
      margin-bottom: 0;
    }
  }
  /deep/ .dialog-footer {
    button {
      width: 30%;
    }
  }
}
</style>